@import url(https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400;700&family=Vibur&display=swap);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
	font-size: 30px;
}

@media screen and (max-height: 900px) {
	html {
		font-size: 25px;
	}
}
@media screen and (max-height: 750px) {
	html {
		font-size: 20px;
	}
}

.Button{color:#fff;background-color:#000;box-shadow:0px 10px 30px -5px rgba(0,0,0,.2);cursor:pointer;text-decoration:none;text-transform:uppercase;font-size:.75rem;font-weight:700;padding:.5rem 1.5rem;margin:0 .25rem;border-radius:.5rem;border:0;will-change:transform}.Button a{display:block;color:#fff;text-decoration:none;padding:.5rem 1.5rem;margin:-0.5rem -1.5rem}.Button.is-hidden{opacity:0;pointer-events:none}.Button.is-disabled{pointer-events:none;background-color:#878787}.Button.is-accent{background-color:#6fc876}.Button:focus{outline:0}
.Header{font-family:"Vibur",cursive;font-size:2.5rem;color:#fff;text-shadow:1px 0px 6px #000;margin:0 0 .5rem .5rem}
.Intro{width:16rem;background:#fff;border-radius:1rem;box-shadow:0px 5px 30px -5px rgba(0,0,0,.2);position:relative;color:#272727;padding:1.5rem 2rem;box-sizing:border-box}.Intro .Header{width:100%;margin:0;text-align:center;position:absolute;top:-5rem;left:0;font-size:4rem}.Intro__description{font-size:.7rem;font-weight:300;margin-bottom:.25rem;line-height:.9rem}.Intro__description .is-app-name{font-weight:400}.Intro__setup{display:flex;flex-direction:column;align-items:center;margin-top:1rem}.Intro__info{display:flex;align-items:center;font-size:.7rem;margin-bottom:.75rem;text-align:center;height:1.5rem}.Intro__info.has-error{color:#b30309}
.Error{color:#272727;background-color:#fff;box-sizing:border-box;width:10rem;position:absolute;left:calc(50% - 5rem);border-radius:1rem;box-shadow:0px 5px 30px -5px rgba(0,0,0,.2);padding:.5rem .75rem;display:flex;flex-direction:column;align-items:center;margin-top:1.5rem}.Error .Button{-webkit-transform:scale(0.6);transform:scale(0.6);margin-top:.5rem}.Error__header{font-size:.8rem;font-weight:bold;margin-bottom:.5rem}.Error__description{font-size:.6rem;font-weight:300;text-align:center}
.Icon{cursor:pointer;flex-shrink:0}.Icon>svg{width:100%;height:100%}
.Player{position:relative;max-width:15rem}.Player>.Button{position:absolute;top:-2.5rem;right:-1rem;padding:.5rem .75rem;border-radius:1rem}.Player__cover{width:15rem;height:15rem;border-radius:1rem;box-shadow:0px 5px 30px -5px rgba(0,0,0,.2);background:linear-gradient(0deg, #C6FFDD 0%, #FBD786 40%, #f7797d 100%);background-size:cover;background-position:center}.Player__buttons{position:absolute;bottom:-2.5rem;width:100%;display:flex;justify-content:center}.Player__label{font-size:.8rem;text-align:center;margin:.5rem 0;font-weight:300;width:100%;height:1.5em;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}
.TimerPlayer__wrapper{position:relative}.TimerPlayer__time{position:absolute;top:calc(50% - 1.5rem);width:100%;text-align:center;font-weight:700;line-height:2rem;font-size:2rem;color:#fff;text-shadow:1px 0px 8px #000}.TimerPlayer__time-description{font-size:.8rem;font-weight:400}.TimerPlayer .Error{position:absolute;top:0}
.Playlist{width:6rem;height:6rem;background:#000;margin:.25rem;overflow:hidden;border-radius:2px;cursor:pointer;box-shadow:0 0 15px 2px rgba(0,0,0,.2);background-size:cover}
.Playlists{box-sizing:border-box;display:flex;flex-wrap:wrap;justify-content:center;height:100%;width:100%;overflow:auto}.Playlists::-webkit-scrollbar{display:none}
.EditBox{width:100%;margin-bottom:1rem}.EditBox__label{font-weight:300;font-size:.8rem}.EditBox__content{display:flex;border-bottom:1px #f0f0f0 solid;font-size:.9rem;padding:0 0 .1rem;margin-top:.5rem;cursor:default}.EditBox__content-text{white-space:nowrap;overflow:hidden;text-overflow:ellipsis;padding-right:.25rem;flex-grow:1}.EditBox__content-edit{cursor:pointer}
.InputNumber{width:100%;display:flex;align-items:center}.InputNumber.has-error .InputNumber__input{border-color:#b30309;color:#b30309}.InputNumber__label,.InputNumber__suffix{font-weight:300;font-size:.8rem}.InputNumber__input{-webkit-appearance:textfield;appearance:textfield;margin:0 .25rem 0 .75rem;background:rgba(0,0,0,0);border:0;border-bottom:1px #f0f0f0 solid;color:#f0f0f0;width:3rem;font-size:.8rem}.InputNumber__input::-webkit-inner-spin-button,.InputNumber__input::-webkit-outer-spin-button{-webkit-appearance:none;margin:0}.InputNumber__input::-webkit-input-placeholder{color:#f0f0f0;opacity:.5}.InputNumber__input::placeholder{color:#f0f0f0;opacity:.5}.InputNumber__input:focus{outline:none}
.PlaylistsSelector{height:20rem;width:16rem;background:linear-gradient(0deg, #C6FFDD 0%, #FBD786 40%, #f7797d 100%);background-size:cover;background-position:center;box-shadow:0px 5px 30px -5px rgba(0,0,0,.2);color:#f0f0f0;border-radius:1rem;position:relative;overflow:hidden}.PlaylistsSelector__form{width:calc(100% - 2rem);position:absolute;bottom:0;background:rgba(0,0,0,.4);box-shadow:0px 5px 10px 5px rgba(0,0,0,.3);border-radius:1rem;padding:1rem;-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px)}
.Setup{position:relative}.Setup__buttons{position:absolute;bottom:-2.5rem;width:100%;display:flex;justify-content:space-between}
.App{width:100%;height:100vh;color:#272727;background:#f0f0f0;position:relative;display:flex;align-items:center;justify-content:center;font-family:"Lato",sans-serif}
